import {
  select,
  scaleLinear,
  scaleOrdinal,
  schemeTableau10,
  scaleTime,
  extent,
  axisLeft,
  axisBottom,
  line,
  curveBasis,
  group
} from 'd3';

import { colorLegend } from './colorLegend';


const svg = select('#diagram');
const width = +svg.attr('width');
const height = +svg.attr('height');

const margin = { top: 60, right: 200, bottom: 80, left: 105 };
const innerWidth = width - margin.left - margin.right;
const innerHeight = height - margin.top - margin.bottom;
const colorScale = scaleOrdinal(schemeTableau10);

let xAxis, yAxis, xAxisLayer, yAxisLayer, chartLayer, lineGenerator, titleLayer

let xScale = scaleTime()
let yScale = scaleLinear()




export const initializeGraph = (selection) => {

  const yAxisLabel = 'Power (kWh)';
  const xAxisLabel = 'Time';

  selection.append('g').attr("class","xAxisLayer")
  selection.append('g').attr("class","yAxisLayer")
  xAxisLayer = selection.selectAll(".xAxisLayer");
  yAxisLayer = selection.selectAll(".yAxisLayer");

  selection.append('g')
    .attr('transform', `translate(${width-margin.right},${margin.top})`)
      .call(colorLegend, {
        colorScale,
        circleRadius: 13,
        spacing: 30,
        textOffset: 15
      });

  selection.append("rect")
    .attr("class", "chartBG")
    .attr("width",innerWidth)
    .attr("height",innerHeight)
  
  yAxisLayer.append('text')
    .attr('class', 'axis-label')
    .attr('y', -60)
    .attr('x', -innerHeight / 2)
    .attr('fill', 'black')
    .attr('transform', `rotate(-90)`)
    .attr('text-anchor', 'middle')
    .text(yAxisLabel);

  xAxisLayer.append('text')
    .attr('class', 'axis-label')
    .attr('y', 80)
    .attr('x', innerWidth / 2)
    .attr('fill', 'black')
    .text(xAxisLabel);
}

export const renderGraph = (data, props, selection, title)=> {
    
    const xValue = d => d.timestamp;

    
    const yValue = d => d.power;

    chartLayer = selection.selectAll(".chartLayer");
    chartLayer.attr('transform', `translate(${margin.left},${margin.top})`);
    xAxisLayer = selection.selectAll(".xAxisLayer");
    yAxisLayer = selection.selectAll(".yAxisLayer");
    titleLayer = chartLayer.selectAll('.title').datum(title)


    xScale
      .domain(props.x)
      .range([0, innerWidth])
     // .nice();
    
    yScale
      .domain(props.y)
      .range([innerHeight, 0])
    //  .nice();
    
    xAxis = axisBottom(xScale)
    //.tickSize(-innerHeight)
    //.tickPadding(15);

    yAxis = axisLeft(yScale)
    .tickSize(-innerWidth)
    .tickPadding(10);
    
    
    
    yAxisLayer
      .transition().duration(750)
      .call(yAxis);

    //yAxisLayer.selectAll('.domain').remove();
    titleLayer.enter().append('text')
      .attr('class', 'title')
      .attr('y', -10)
      .merge(titleLayer)
        .text(title);


    
    xAxisLayer
      .attr('transform', `translate(0,${innerHeight})`)
      .transition().duration(750)
        .call(xAxis)
    
   // xAxisLayer.select('.domain').remove();
    

    
    lineGenerator = line()
      .x(d => xScale(xValue(d)))
      .y(d => yScale(yValue(d)))
      .curve(curveBasis);
    

    
    colorScale.domain(data.keys());
    console.log(Array.from(data.values()))
    let lines = chartLayer.selectAll('.line-path').data(Array.from(data.values()))

    lines.enter().append('path')
      .attr('class', 'line-path')
      .merge(lines)
      //.transition().duration(1000)
        .attr('d', d => lineGenerator(d))
        .attr('stroke', d=> colorScale(d[0].device));

        lines.exit().remove()
  };