import {
    group
  } from 'd3';

export const groupData = (data) =>{
  const dataGrouped = group(data,d => d.device);
  dataGrouped.forEach((v,k,m) => v.sort((a,b)=>a.timestamp-b.timestamp));
  return dataGrouped;
}

export const getLatest = (dataGrouped) =>{
  //TODO handle empty, handle stale
  let dataMap = new Map();
  dataGrouped.forEach((v,k,m) => dataMap.set(k,v[v.length-1]))
  return dataMap
}

export const mergeMaps = (map1, mapUpdate) =>{
  let mapMain = map1
  mapUpdate.forEach((v,k,m) => mapMain.set(k,mapMain.get(k).concat(v)))
  return mapMain
}

export const getStamp = (data) =>{
  return data[0].timestamp
}