import {
    shuffle,
    range
  } from 'd3';



  const round = (x,n) =>{
    return Math.round(x*10**n)/10**n;
}

const mockRadiation = (d,t) =>{
    const dailyRadiation = d
    const output = (0.5+Math.random())/(288/dailyRadiation)*(-1*(t-5)*(t-17))
    //quadratic Model
    return Math.max(output,0)
}
const mockSolar = (r) =>{
    const performance = 0.75*0.15*40//Area*PerformanceRatio*Yeild
    return r*performance
}

const mockWindSpeed= (t) => {
    const maxi =   15
    return maxi + maxi*Math.sin(t+Math.random())
}
const mockWind= (w,a) => {
    const sweptArea = 1*1.5
    return  0.3*0.5*a*w**3*sweptArea/1000
}

const mockConsumption = (t) => {
    const tn = t+4*Math.random()-2
    const m = 2
    const s = 5
    return 50*Math.exp(-0.5*((tn-m)/s)**2)+1

}

const mockDC = (t) =>{
    if(t==8||t==10||t==6){
        return 50+10*Math.random()
    }else{
        return 0
    }
} 
const mockUsage = (d,t) =>{
    const v = round(mockWindSpeed(t),1)
    const w = round(mockWind(v,1.2),1)

    const r = round(mockRadiation(d,t),1)
    const s = round(mockSolar(r),1)

    const c = round(mockConsumption(t),1)
    const dc = round(mockDC(t),1)
    let b = 0
    //If the Battery needs to help
    b = round(c+dc-s-w,1)
    if(b>0){
        b = Math.min(b,5)
    //If the Battery can charge
    }
    const g = round(-w-s+c+dc-b,1)
    return [r,s,v,w,b,-dc,-c,g]

    
}

const mockTimeStamps = (start,step,n) =>{
    let times = []
    range(start,start-n,-step).forEach(t =>{
        times.push(new Date(t))
    })
    return times
}

export const generateData = (frequency,duration,time) =>{
    const timestamps = mockTimeStamps(time,frequency,duration)
    const group = "EZONE"
    let devices = ["Solar Irradiance","Solar Panels","Wind Speed","Wind Turbine", "Battery","DC Charger", "Consumption","Grid"];
    let types = ["solar","solar","wind","wind", "battery","charger","conusmption","grid"];
    let data = []
    //todo use maps instead of array index matching
    timestamps.forEach(timestamp=>{
        const usage = mockUsage(timestamp.getDate(),timestamp.getHours())
        usage.forEach((power,index)=>{
            data.push(
                {
                    timestamp: timestamp,
                    group:group,
                    device: devices[index],
                    type: types[index],
                    power: power
                }
            )
            }
        )
    });
    return data
}