import {
    select,
    scaleLinear,
    scaleOrdinal,
    schemeCategory10,
    scaleTime,
    extent,
    axisLeft,
    axisBottom,
  } from 'd3';
import { renderDiagram } from './renderHubSpoke';
import { renderGraph, initializeGraph } from './renderLineGraph';
import {generateData} from './generateMockDataEzone';
import {getLatest, groupData, mergeMaps} from './getLatestData';


const display = () => {
  if(displayingGraph){
    //svg.selectAll("*").remove();
    if(intervalID!=-1){
      clearInterval(intervalID)
      intervalID = -1
    }
    renderGraph(dataGroup, chartProps, svg, period)
  }else{
    intervalID = renderDiagram(getLatest(dataGroup));
  }
}

/*
* @todo Implement button layout and interactivity properly
*/
const changeTimeScale = ()=>{
  dataView = data.filter(d => {
    return((time-d.timestamp.getTime())<view)})
  chartProps.x = extent(dataView, d => d.timestamp)
  chartProps.y = extent(dataView, d => d.power)
  dataGroup = groupData(dataView)
  display()
}
window.changeTimeScale = changeTimeScale

const refreshData = ()=>{
  data = generateData(interval,getMS(31,0),time)
  changeTimeScale(view)
}
window.refreshData = refreshData

window.updateDisplay = ()=>{
    const timeOld = time
    time = time + interval*Math.round(10*Math.random())
    let dataNew = generateData(interval,time-timeOld,time)
    data = data.concat(dataNew)
    console.log(data)
    changeTimeScale()
    
}

window.toggleDisplay = ()=>{
  svg.selectAll("*").remove();
  displayingGraph = !displayingGraph
  if(displayingGraph){
    svg.append('g').attr("class","chartLayer")
    initializeGraph(svg.select(".chartLayer"))
  }else{
    svg.append('g').attr("class","edgeLayer");
    svg.append('g').attr("class","nodeLayer");
    svg.append('g').attr("class","textLayer");
  }
  display()
}
 
const renderUIChart = (data) =>{
  // Initialize the button
  const dropdownButton = select("#chart_options")
  .append('select')

  dropdownButton.selectAll('myOptions').data(data).enter().append('option')
    .text((d) => { return d; }) // text showed in the menu
    .attr("value", d => { return d; }) // corresponding value returned by the button

  dropdownButton.on("change", (d) => {
    period = dropdownButton.property("value")
    view = chartOptions.get(period)
    changeTimeScale()
  })
}

const getMS = (days, hours) => {
  return (days*24+hours)*60*60*1000
}




const svg = select('#diagram');
const width = +svg.attr('width');
const height = +svg.attr('height');
  
svg.append('g').attr("class","edgeLayer");
svg.append('g').attr("class","nodeLayer");
svg.append('g').attr("class","textLayer");

var intervalID = -1
var chartOptions = new Map()
chartOptions.set("day",getMS(1,0))
chartOptions.set("week",getMS(6,0))
chartOptions.set("month",getMS(31,0))
var period = "day"
var view  = chartOptions.get(period)
let displayingGraph = false;
let time = new Date().getTime()
var data,dataView,dataGroup,chartProps = {}
const interval = getMS(0,1)



renderUIChart(Array.from(chartOptions.keys()))
refreshData()
